<template>
  <v-dialog :value="active" overlay-color="primary" overlay-opacity=".9">
    <v-list color="primary" nav tile elevation="0">
      <v-list-item
        v-for="item in tabs"
        :key="item.name"
        @click="clickHandler(item)"
        active-class="test"
        class="text-center"
      >
        <v-list-item-content>
          <v-icon color="white">{{ item.icon }}</v-icon>
          <v-list-item-title class="test"><h2>{{ item.name}}</h2></v-list-item-title>
          <v-list v-if="item.expanded" color="primary" nav tile elevation="0">
            <v-list-item @click="clickHandler(option)" v-for="option in item.options" :key="option.name" class="text-center">
              <v-list-item-content>
              <v-list-item-subtitle style="color:white">{{option.name}}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-list-item-content>
      </v-list-item>
      <v-list-item
        href="https://blueberryhillinn.com"
        target="_blank"
        @click="$emit('close')"
        active-class="test"
        class="text-center"
      >
        <v-list-item-content>
          <v-img
            class="center"
            src="@/assets/bbh_logo.png"
            max-width="40"
            transition="scale-transition"
          />
          <v-list-item-title class="test"><h2>Blueberry Hill Inn</h2></v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-dialog>
</template>

<script>
export default {
  data: () => ({
    tabs: [
      {
        index: 0,
        name: "Home",
        icon: "mdi-home",
        link: "/",
      },
      {
        index: 1,
        name: "Trails",
        icon: "mdi-hiking",
        expanded: false, 
        options: [{name: 'Conditions', link: '/conditions'}, {name: 'Descriptions', link: '/trails'},]
      },
      {
        index: 2,
        name: "Visit / Rentals",
        icon: "mdi-ski-cross-country",
        link: "/visit"
      },
      {
        index: 3,
        name: "Trail Conditions",
        icon: "mdi-weather-partly-snowy",
        link: "/conditions"
      },
      {
        index: 4,
        name: "About",
        icon: "mdi-information",
        link: "/about"
      }
    ]
  }),
  props: {
    active: Boolean
  },
  methods: {
    clickHandler : function (i) {      
      if (i.options) {
        i.expanded = !i.expanded;
        console.log(i.options);
      }else {
        this.$router.push(i.link);
        this.$emit('close');
      }
      
    }
  }
};
</script>

<style scoped>
.test {
  color: white;
}
.center {
  margin: auto;
  width: 50%;
  padding: 10px;
}
</style>
